@font-face {
  font-family: OwnFonts;
  src: url("PressStart2P-Regular.492382e8.ttf");
}

*, :before, :after {
  box-sizing: border-box;
  user-select: none;
}

body {
  background: linear-gradient(#00b2f0 58.3%, #fad619 32%);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.world {
  aspect-ratio: 100 / 40;
  position: relative;
  overflow: hidden;
}

.item {
  width: 5%;
  height: 5%;
  bottom: calc(var(--bottom) * 1%);
  left: calc(var(--left) * 1%);
  background-size: contain;
  position: absolute;
}

.score {
  font-family: OwnFonts;
  font-size: 3vmin;
  position: absolute;
  top: 1vmin;
  right: 1vmin;
}

.start-screen {
  width: 900px;
  font-family: OwnFonts;
  font-size: 7vmin;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ground {
  --left: 0;
  width: 300%;
  bottom: 0;
  left: calc(var(--left) * 1%);
  position: absolute;
}

.dino {
  --bottom: 0;
  height: 40%;
  left: 1%;
  bottom: calc(var(--bottom) * 1%);
  position: absolute;
}

.cactus {
  left: calc(var(--left) * 1%);
  height: 16%;
  position: absolute;
  bottom: 0;
}

.start-screen {
  color: #fff;
  text-align: center;
  font-size: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  display: none;
}

[data-world] {
  width: 100%;
  height: 100%;
  position: relative;
}

.start-screen {
  text-align: center;
  color: #fff;
  z-index: 10;
  width: 90%;
  font-size: 5vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (width <= 768px) {
  .start-screen {
    font-size: 7vw;
  }
}

.start-screen.game-over {
  font-size: 7vw;
}

[data-world] {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.start-text {
  color: #fff;
  font-size: 15px;
}
